"use client";

import * as React from "react";
import { useRef } from "react";
import Image from "next/image";
import { ChevronLeft, ChevronRight, Pencil, Trash2 } from "lucide-react";
import { Card, CardContent } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import {
  Carousel,
  CarouselContent,
  CarouselItem,
  CarouselNext,
  CarouselPrevious,
} from "@/components/ui/carousel";
import {
  Dialog,
  DialogContent,
  DialogHeader,
  DialogTitle,
  DialogTrigger,
  DialogClose,
} from "@/components/ui/dialog";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Button } from "@/components/ui/button";
import { useToast } from "@/hooks/use-toast"; // Ensure this is correct
import { Product } from "@/types/product";

interface ProductCarouselProps {
  products: Product[];
}

export function ProductCarousel({ products: initialProducts }: ProductCarouselProps) {
  const [products, setProducts] = React.useState(initialProducts);
  const [editingProduct, setEditingProduct] = React.useState<Product | null>(null);
  const { toast } = useToast();
  const closeDialogRef = useRef<HTMLButtonElement>(null);

  const handleEdit = (product: Product) => {
    setEditingProduct({ ...product });
  };

  const handleDelete = (productId: number) => {
    const deletedProduct = products.find((p) => p.id === productId);
    setProducts(products.filter((p) => p.id !== productId));
    if (deletedProduct) {
      toast({
        title: "Product Deleted",
        description: `"${deletedProduct.name}" has been removed from the catalogue.`,
      });
    }
  };

  const handleEditSubmit = (e: React.FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    if (editingProduct) {
      const updatedProduct = {
        ...editingProduct,
        updatedAt: new Date().toISOString(),
      };
      setProducts(products.map((p) => (p.id === updatedProduct.id ? updatedProduct : p)));
      setEditingProduct(null);
      toast({
        title: "Changes Saved",
        description: `Product "${updatedProduct.name}" has been updated.`,
      });
      closeDialogRef.current?.click(); // Close the dialog
    }
  };

  return (
    <>
      <Carousel className="w-full">
        <CarouselContent>
          {products.map((product) => (
            <CarouselItem key={product.id} className="overflow-y-auto">
              <Card className="w-full h-full">
                <CardContent className="flex flex-col p-6 h-full">
                  {/* Product Details */}
                  <div className="flex flex-col md:flex-row gap-6">
                    <div className="w-full md:w-1/2">
                      <Image
                        src={product.imageUrl}
                        alt={product.name}
                        width={400}
                        height={400}
                        className="w-full h-auto rounded-lg object-cover"
                      />
                    </div>
                    <div className="w-full md:w-1/2">
                      <div className="flex justify-between items-center mb-2">
                        <h2 className="text-2xl font-bold">{product.name}</h2>
                        <div className="flex gap-2">
                          <Dialog>
                            <DialogTrigger asChild>
                              <Button variant="outline" size="icon" onClick={() => handleEdit(product)}>
                                <Pencil className="h-4 w-4" />
                              </Button>
                            </DialogTrigger>
                            <DialogContent className="sm:max-w-[425px] max-h-[80vh] overflow-y-auto">
                              <DialogHeader>
                                <DialogTitle>Edit Product</DialogTitle>
                              </DialogHeader>
                              <form onSubmit={handleEditSubmit} className="space-y-4">
                                {editingProduct && (
                                  <>
                                    <div className="grid grid-cols-2 gap-4">
                                      <div>
                                        <Label htmlFor="sku">SKU</Label>
                                        <Input
                                          id="sku"
                                          value={editingProduct.sku || ""}
                                          onChange={(e) =>
                                            setEditingProduct({
                                              ...editingProduct,
                                              sku: e.target.value,
                                            })
                                          }
                                        />
                                      </div>
                                    </div>
                                    <Button type="submit">Save Changes</Button>
                                  </>
                                )}
                              </form>
                              <DialogClose ref={closeDialogRef} className="hidden" />
                            </DialogContent>
                          </Dialog>
                          <Button variant="outline" size="icon" onClick={() => handleDelete(product.id)}>
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    </div>
                  </div>
                </CardContent>
              </Card>
            </CarouselItem>
          ))}
        </CarouselContent>
        <CarouselPrevious />
        <CarouselNext />
      </Carousel>
    </>
  );
}
